@font-face {
  font-family: "MuseoW01-900";
    src: url(./assets/MuseoW01-900.woff2) format("woff2"), /* chrome firefox */
    url(./assets/MuseoW01-900.ttf) format("truetype"), /* chrome firefox opera Safari, Android, iOS 4.2+*/
}

*, *:before, *:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "MuseoW01-900";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
